.button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px 24px;
    border-radius:32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    margin-left: 3rem;
    margin-bottom: 166px;

  }
  
  .button1 {
    background-color: #4CAF50; 
    color: white; 
    border: 2px solid #4CAF50;
  }
  
  .button1:hover {
    background-color: #004d29;
    color: white;
  }
  @media screen and (max-width: 1000px) {
    .textbox{
        width: 100%;
        margin-left: 0px;
        height: auto;
        padding-top: 10em;
        background: linear-gradient(to bottom, #06616300, #fdffff)
    }
    
    
}
.activeHead{
    background-color: #ffffff;
    color: #0f8400;
    font-size: 25px;
    font-weight: bolder;
    text-align: center;
    display: none;
}
@media screen and (max-width: 500px) {
    .activeHead{
        display: block;
