.MenuImg{
    width: 100%;
    height: auto;
    /* background-color: green; */
    background: url('top-view-delicious-chicken-salad-with-mayyonaise-vegetables-white-desk-snack-ripe-color-meat-meal-salad.jpg') no-repeat center;
    overflow: hidden;
    background-size: cover;
   
}
.specialCard{
    width: 60%;
    height: auto;
    overflow: hidden;
    background-color: transparent;
    margin-left: 3rem;
}
.InnerCard{
    width: 90%;
    height: auto;
    overflow: hidden;
    background-color: transparent;
    margin: auto;
    margin-left: 3rem;
}
.cardBody{
    width: 200px;
    height: 330px;
    float: left;
    background-color: transparent;
    margin: .9rem;
    text-align: center;
    
}
.cardM{
    width: 200px;
    height: 200px;
    background-color: #fff;
    background-image: url('samplebiri.jpg');
    background-size: cover;
    border: 4px solid #fff;
}
.btnCart3 {
    height: 2.1rem;
    width: 10rem;
    border-radius: 15px;
    font-weight: bold;
    background-color: #fff;
    color: #0f8400;
    border: 2px solid #0f8400;
    
}

.btnCart3:hover {
    color: #ffffff;
    background-color: #0f8400;
}
.Title3 {


    font-size: 20px;
    font-weight: bold;
    margin-bottom: -.5rem;
}

.Title4 {
    /* float: right; */
    font-size: 20px;
    font-weight: bold;

}
.Our{
    width: 100%;
    margin: auto;
    text-align: center;
    font-size: 60px;
    font-family: 'Satisfy', cursive;
    color: orange;
    margin-top: 2rem;
}
.spMenu{
    width: 100%;
    text-align: center;
    margin-top: -2.6rem;
    color: #0f8400;
    font-weight: bold;
    margin-bottom: 5%;
}
@media screen and (max-width: 1100px) {
    .specialCard{
        width: 100%;
        margin-left: 0px;
        text-align: center;
    }
    .InnerCard{
        width: 100%;
        margin-left: 0px;
    }
   
    }
    @media screen and (max-width: 500px) {
        .cardBody{
            background-color: transparent;
            width: 90%;
            margin-right: 5%;
            
        }

        .cardM{
            width: 100%;
            margin: 2rem;
            margin: auto;
        }
    }
 
