.navM-links {
    display: flex;
    list-style: none;

}

.navM-item:not(:first-of-type) {
    margin-left: .7em;
    margin-right: .7rem;
}

.navM-brand {
    color: rgb(0, 0, 0);
    font-size: 16px;
    font-family: 'Play', sans-serif;
    font-family: 'Vollkorn', serif;
}

.navM-link {
    color: rgb(0, 0, 0);
    text-decoration: none;
    font-size: 11px;
}

.navbarM {
    width: 95%;
    height: 7.5vh;
    color: rgb(0, 0, 0);
    margin: auto;

}

.navbarM {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

hr {
    width: 95%;
    margin: auto;
}

@media screen and (max-width: 800px) {
    .navbarM {
        display: none;

    }
}
#muIcon{
    color: #00000065;
    font-size: 16px;
    margin-top: .3rem;
    /* margin-right: -.3rem; */
}