.blogcardbody{
    width: 100%;
    height: auto;
    /* background-color: transparent; */
    background-image:linear-gradient(to top, #06616300, #fdffff), url('https://img.freepik.com/free-psd/tropical-green-leaves-background_24972-1942.jpg?w=1380&t=st=1671003614~exp=1671004214~hmac=7e10055300bac2abc06d3aa3205123989290ec279a46dff7dc52b96cd563263c');

    text-align: center;
    background-size: cover;
    overflow: hidden;
}
.blogSinglecard{
    width: 30%;
    height: 350px;
    background-color: transparent;
    margin: 1rem;
    float: left;

}
.blogDate{
    width:20%;
    height: 80px;
    border:2px solid black;
    float: left;
    margin: .5rem;
    margin-right: .1rem;
    margin-left: .1rem;
    background-color: rgba(255, 255, 255, 0.975);
    height: auto;
    overflow: hidden;
    
}
.blogContent{
    width: 70%;
    min-height: 300px;
    border:2px solid black;
    float: left;
    margin: .5rem;
    background-color: rgba(255, 255, 255, 0.975);
    height: auto;
    overflow: hidden;

}
#dateData{
    font-size: 40px;
    font-weight: bold;
    margin-top: 0rem;
    margin-bottom: -.5rem;
}
#monthData{
    font-weight: bold;
}
.Our{
    width: 100%;
    margin: auto;
    text-align: center;
    font-size: 60px;
    font-family: 'Satisfy', cursive;
    color: orange;
    margin-top: 2rem;
}
.spMenu{
    width: 100%;
    text-align: center;
    margin-top: -2.6rem;
    color: #0f8400;
}
#BoldContent{
    font-weight: bold;
    margin: 1rem;
}
#StrContent{
    text-align: left;
    margin: 1rem;
}
@media screen and (max-width: 1000px) {
    .blogSinglecard{
        width: 100%;
    }
}