} */
#BagIcon{
    color: #00000066;
    margin-top: 1.3rem;
    font-size: 25px;
    margin-right: 3rem;

}
#seperator{
    color: #00000066;
    font-weight: bolder;
    margin-top: 1.4rem;
    margin-right: 1rem;
}
@media screen and (max-width: 1000px) {
  .box{
      display: none;
  }
  #seperator{
    display: none;
  }
  #BagIcon{
    display: none;
  }

}

