.bgImg {
    background: url('https://img.freepik.com/free-photo/abstract-yellow-sunshine-theme-summer-watercolor-background-illustration-high-resolution-free-photo_1340-21029.jpg?w=740&t=st=1670908136~exp=1670908736~hmac=aca6504778cfd847974bf6adfebc0df7bb40acd9853924ec54bf649bb56958f7') no-repeat;
    /* height: 150vh; */
    height: auto;
    width: 100%;
    background-size: cover;
    overflow: hidden;


}

.cardcontainer {
    width: 80%;
    margin: auto;
    height: auto;
    background-color: transparent;
    text-align: center;
    overflow: hidden;
    display: flex;
    overflow-y: hidden;
    margin: auto;
    overflow-x: scroll;
    white-space: nowrap;

}
.cardbody {
    width: 200px;
    height: 19em;
    float: left;
    margin: auto;
    background-color: transparent;
    margin-right: 2rem;
    margin-bottom: 2rem;
    margin: 2rem;
    
}

@media screen and (max-width: 800px) {
    .cardcontainer {
        width: 95%;
    }

}



.ImgContainer {
    width: 200px;
    height: 200px;
    background-image: url(samplebiri.jpg);
    background-size: cover;
    border: 4px solid white;
    
    /* background-color: rgb(117, 117, 117); */
}

.Price {
    margin-top: -1rem;
    font-weight: bold;
}

.cartName {
    font-weight: bold;
}

.HeadComo {
    text-align: center;
    margin-bottom: 3rem;
    font-size: 50px;
    margin-bottom: -2.5rem;
    color:orange;
    font-family: 'Satisfy', cursive;
}
.HeadComo2 {
    text-align: center;
    margin-bottom: 3rem;
    font-size: 30px;
    font-weight: bold;
    color: #0f8400;
}

.ImageLarge {
    width: 100%;
    height: auto;
    overflow: hidden;
    background-color: transparent;
    padding-bottom: 5rem;
}

.strbImg {
    width: 650px;
    /* max-width: 650px; */
    height: 350px;
    background-image: url("https://img.freepik.com/free-photo/side-view-fresh-strawberry-white-background_141793-7194.jpg?w=1380&t=st=1670907132~exp=1670907732~hmac=2b086625fe03e8e7cad9db7999efdcb427897b5d98df50eb1b605ba1e8979d52");
      background-size: cover;
    /* background-color: azure; */
    margin-left: 3rem;
    margin-top: 3rem;
    float: left;
}
.strText {
    width: 500px;
    /* height: auto;
    overflow: hidden; */
    background-color: transparent;
    float: left;
    margin-top: 3rem;

}
#Title1 {


    font-size: 60px;
    margin-bottom: -2rem;
    font-family: 'Satisfy', cursive;
    margin-left: -2rem;
    margin-top: -.2rem;
}

#Title2 {
    /* float: right; */
    font-size: 30px;
    color: #0f8400;
    margin-left: 3rem;
    font-weight: bold;
    

}
@media screen and (max-width: 1100px) {
    .strbImg{
        width: 100%;
        margin-left: 0; 
        

    }
    .strText{
        width: 100%;
        text-align: center;
        margin: 0px;
        margin-top: 2rem;
        
       
    }
    #Title1{
        margin-left: -2rem;
        font-size: 40px;
        
        
    }
    #Title2{
        margin-left:-2rem;
        font-size: 25px;
    }
    .ContentSide{
        margin-left: -2rem;
        margin-right: 2rem;
        height: auto;
        overflow: hidden;
    }

}




.btnCart {
    height: 2.3rem;
    width: 10rem;
    border-radius: 15px;
    font-weight: bold;
    background-color: #fff;
    color: #0f8400;
    border: 2px solid #0f8400;
}

.btnCart:hover {
    color: #ffffff;
    background-color: #0f8400;
}

.btnCart2 {
    height: 3rem;
    width: 10rem;
    border-radius: 15px;
    font-weight: bold;
    color: #ffffff;
    background-color: #0f8400;
    border: 2px solid #0f8400;
    margin-top: 2rem;
}

.btnCart2:hover {
    background-color: rgb(3, 50, 3);
    color: #ffffff;
}
.ContentSide{
    margin-left: 3rem;
}